<template>
    <div>
        <tpldescription ref="expenseinclude" :productid="productid" modeltype="4" pname=""></tpldescription>
        <tpldescription ref="expenseexclude" :productid="productid" modeltype="5" pname=""></tpldescription>
        <tpldescription ref="expensechild" :productid="productid" modeltype="7" pname=""></tpldescription>
    </div>
</template>
<script>
import tpldescription from "@/components/tpldescription"
export default {
    components: { tpldescription },
    data() {
        return {
            productid: $("#productid").val()
        }
    },
    mounted: function () {
        this.loadDatas();
    },
    methods: {
        loadDatas: function () {
            let that = this;
            let loading = that.$loading({ lock: true });
            Promise.all([
                that.$refs.expenseinclude.loadDatas(),
                that.$refs.expenseexclude.loadDatas(),
                that.$refs.expensechild.loadDatas()
            ]).then(res => {
                loading && loading.close();
            }).catch(err => {
                loading && loading.close();
                this.$message({
                    type: 'error',
                    message: err
                })
            });
        },
        saveDatas: function () {
            let that = this;
            let loading = that.$loading({ lock: true });
            Promise.all([
                that.$refs.expenseinclude.checkTplData(),
                that.$refs.expenseexclude.checkTplData(),
                that.$refs.expensechild.checkTplData()
            ]).then(res => {
                return Promise.all([
                    that.$refs.expenseinclude.saveTplData(),
                    that.$refs.expenseexclude.saveTplData(),
                    that.$refs.expensechild.saveTplData()
                ]);
            }).then(resList => {
                loading && loading.close();
                //
                var result = {
                    success: true,
                    msg: ''
                };
                resList.forEach((v, i) => {
                    result.success = result.success && v.success;
                    v.msg && (result.msg += `<p>${v.msg}</p>`);
                });
                that.$confirm(result.msg, "", {
                    confirmButtonText: "",
                    type: result.success ? "success" : "warning",
                    center: true,
                    showCancelButton: false,
                    dangerouslyUseHTMLString: true
                }).then(_ => {

                });
            }).catch(err => {
                this.$message({
                    type: 'error',
                    message: err
                })
                loading && loading.close();
            });
        }
    }
}
</script>

<style scoped>
.skin-blue label {
  display: inline !important;
  max-width: 100%;
  /* margin-bottom: 5px; */
  /* font-weight: 700 !important; */
}

.skin-blue .radio > label {
  position: relative;
  padding-left: 25px;
  margin-right: 0px;
  color: #666666;
}

.skin-blue .checkbox > label {
  position: relative;
  cursor: pointer;
  margin-right: 0px;
  padding-left: 25px;
}

.inline {
  display: inline-block;
  vertical-align: middle;
}
</style>




